body {
    margin: 0;
    font-family: Arial, sans-serif;
  }
  
  nav {
    position: absolute; 
    top: 15x; 
    right: 0; 
    left: 0;
    background-color: skyblue;
    padding: 1rem; 
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); 
    display: flex; 
    justify-content: space-between; 
    align-items: center; 
  }
  
  .logo {
    color: white;
    font-size: 1.5rem; 
    font-weight: bold; 
  }
  
  nav ul {
    list-style: none;
    display: flex;
    justify-content: flex-end; 
    gap: 1rem;
    margin: 0; 
  }
  
  nav a {
    color: white;
    text-decoration: none;
  }
  
  nav a:hover {
    text-decoration: underline;
  }

  .home {
    background-image: url('./asserts/squad.jpg'); 
    background-size: cover; 
    background-position: center; 
    height: 130vh; 
    color: white; 
    display: flex; 
    flex-direction: column; 
    justify-content: center; 
    align-items: center; 
    text-align: center; 
  }

  .home h2{
    margin-bottom: 1rem;
    font-size: 4rem; 
    font-weight: 500; 
    text-align: center;
    color: black;
  }
  
  .about {
    background-image: url('./asserts/about.jpg'); 
    background-size: cover; 
    background-position: center; 
    height: 130vh; 
    color: black; 
    display: flex; 
    flex-direction: column; 
    justify-content: center; 
    align-items: center; 
    text-align: center; 
  }
  
  .about h2 {
    margin-bottom: 1rem;
    font-size: 4rem; 
    font-weight: 500; 
    text-align: center;
  }
  
  .about h3 {
    margin: 1.5rem 0 1rem; 
    font-size: 3rem; 
    font-weight: 400; 
  }
  
  .about p {
    font-size: 2rem; 
    line-height: 1.6; 
  }
  
  .about ul {
    list-style-type: none; 
    padding: 0;
  }
  
  .about li {
    margin: 0.5rem 0; 
    font-size: 1.5rem; 
  }

  .contact {
    padding: 2rem;
    background-image: url('./asserts/contact.jpg');
    background-color: #f4f4f4; 
    height: 130vh;
    color: #333; 
    text-align: center; 
    font-family: 'Roboto', sans-serif; 
  }
  
  .contact h2 {
    margin-bottom: 1rem;
    font-size: 2.5rem; 
  }
  
  .contact p {
    font-size: 1.2rem; 
    margin-bottom: 2rem;
  }
  
  .contact-form {
    display: flex;
    flex-direction: column; 
    max-width: 500px; 
    margin: 0 auto; 
  }
  
  .form-group {
    margin-bottom: 1rem; 
  }
  
  label {
    display: block; 
    margin-bottom: 0.5rem; 
  }
  
  input,
  textarea {
    width: 100%; 
    padding: 0.5rem; 
    border: 1px solid #ccc; 
    border-radius: 5px; 
  }
  
  textarea {
    resize: vertical; 
    min-height: 100px; 
  }
  
  button {
    padding: 0.7rem; 
    background-color: #007B7F; 
    color: white; 
    border: none;
    border-radius: 5px; 
    cursor: pointer; 
    font-size: 1rem; 
  }
  
  button:hover {
    background-color: #005f5f; 
  }
  
  